export default {
	data() {
		return {
			
		};
	},
	props: {
		postData: {
			type: Object,
			default: ()=>({})
		},
		btnText: {
			type: String,
			default: '接单'
		}
	},
	filters:{
		filterOverTime(time){
			const overtime = time == 0 ? '7天' : time == 1 ? '15天' : '30天'
			return `${overtime}`
		}
	},
	methods:{
		showDetail(){
			const id = this.postData.id || 1
			this.$emit('showDetail',id)
		}
	}
}
